import {ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {environment} from "../../../environments/environment";
import {ChartsService} from "../../utils/charts.service";
import {Series} from "@swimlane/ngx-charts";
import {Subscription} from "rxjs";
import {MultiValuesGraphComponent} from "../../graph/multi-values-graph/multi-values-graph.component";
import {ChatInterface} from "../../utils/messages";
import PlaceStatistics = ChatInterface.PlaceStatistics;
import PlaceStatisticsPerInterval = ChatInterface.PlaceStatisticsPerInterval;
import * as moment from "moment";
import ChartValue = ChatInterface.ChartValue;
import {unitOfTime} from "moment";

@Component({
  selector: 'cabinet-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.scss']
})
export class MainPageComponent implements OnInit, OnDestroy {
  title = environment.title;

  // Реальные графики
  nps_per_day;
  nps_per_day_initial;
  rate_per_day;
  total_per_day;

  // Таблица
  stat: PlaceStatistics[];

  $chart: Subscription;
  $stat: Subscription;

  constructor(public chart: ChartsService, private cdr: ChangeDetectorRef) {
  }

  ngOnInit(): void {
    this.chart.stat("brief");

    this.chart.query('total_per_day');
    this.chart.query('nps_per_day');
    this.chart.query('nps_per_day_initial');
    this.chart.query('rate_per_day');

    this.$chart = this.chart.newChart.subscribe({
      next: (series) => {
        if (!this[series.chart as string])
          this[series.chart as string] = {};
        this[series.chart as string] = series;
      }
    })

    this.$stat = this.chart.newStat.subscribe(stat => {
      this.stat = stat;
    })
  }

  ngOnDestroy() {
    if (this.$chart)
      this.$chart.unsubscribe();

    if (this.$stat)
      this.$stat.unsubscribe();
  }
}
