import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from "rxjs";
import {ChartsService} from "../../utils/charts.service";
import {ChatInterface} from "../../utils/messages";
import PlaceStatistics = ChatInterface.PlaceStatistics;
import * as moment from "moment";
import PlaceStatisticsPerInterval = ChatInterface.PlaceStatisticsPerInterval;
import ChartValue = ChatInterface.ChartValue;
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DetailsComponent implements OnInit, OnDestroy {

  $stat: Subscription;
  $params: Subscription;
  s: PlaceStatistics;
  @Input() id: string;

  constructor(public chart: ChartsService, private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.$params = this.route.params.subscribe({
      next: value => {
        if (value.place_id)
          this.id = value.place_id;
        return this.chart.stat("all", value.place_id);
      }
    });

    this.$stat = this.chart.newStat.subscribe((stat: PlaceStatistics[]) => {
      this.s = stat.find(s => s._id == this.id);

    });
  }

  ngOnDestroy() {
    if (this.$stat)
      this.$stat.unsubscribe();
    if (this.$params)
      this.$params.unsubscribe();
  }
}
