import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {ChatInterface} from "../../../utils/messages";
import * as moment from "moment";
import PlaceStatisticsPerInterval = ChatInterface.PlaceStatisticsPerInterval;
import ChartValue = ChatInterface.ChartValue;


@Component({
  selector: 'app-details-hist-bar-values',
  templateUrl: './details-hist-bar-values.component.html',
  styleUrls: ['./details-hist-bar-values.component.scss']
})
export class DetailsHistBarValuesComponent implements OnChanges {

  @Input() stat: PlaceStatisticsPerInterval;
  series;

  ngOnChanges(changes: SimpleChanges) {
    let time = moment();
    this.series = this.stat.stat.map((s, index) => ({
      name: time.subtract(index, this.stat.interval as moment.unitOfTime.DurationConstructor).format('L'),
      series: s.hist
      })
    ).filter(v=>v.series);
  }
}
