import {ChangeDetectorRef, Component, HostBinding, OnDestroy, OnInit} from '@angular/core';
import {ChatService} from '../../utils/chat.service';
import {Subscription} from 'rxjs';
import {ActivatedRoute, Router} from '@angular/router';
import {ManagerService} from '../../utils/manager.service';
import {Chat, ChatMessageUser, Place, Status, UnreadMsgs, User} from "../../utils/messages";
import * as moment from 'moment';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'chat-manager-chat',
  templateUrl: './manager-chat.component.html',
  styleUrls: ['./manager-chat.component.scss']
})
export class ManagerChatComponent implements OnInit, OnDestroy {
  $active: Subscription;
  $route: Subscription;
  $changes: Subscription;
  membersInternal = false;
  content = true;

  availableChats: (Chat & UnreadMsgs)[];

  get members() {
    return this.membersInternal || !this.chat.activeChat;
  }

  constructor(
    public chat: ChatService,
    public route: ActivatedRoute,
    private router: Router,
    public manager: ManagerService,
    private cdr: ChangeDetectorRef, ) { }

  ngOnInit(): void {
    this.$route = this.route.queryParams.subscribe({
      next: async (value: {place?: string, chat?: string}) => {
        this.membersInternal = false;

        await this.chat.waitHello();

        if (!value.place && !value.chat) {
          if (this.chat.selectFirstUnread() == null) {
            this.togglePlaces(true);
          }
        }

        if (value.place) {
          this.chat.selectPlace({_id: value.place});
          this.updateAviableChats();
        }

        if (value.chat) {
          this.chat.activateChat({_id: value.chat});
        }
      }
    });

    this.chat.active.subscribe({
      next: () => {
        this.membersInternal = this.chat.availableChats && this.chat.availableChats.length > 1;
        this.togglePlaces(false);
      }
    });

    this.chat.availableChatsUpdate.subscribe(() => {
      this.cdr.markForCheck();
    });

    this.$changes = this.chat.changes.subscribe(() => this.updateAviableChats());

  }

  updateAviableChats() {
    // console.log("changes");
    this.availableChats = this.chat.availableChats;
  }

  ngOnDestroy(): void {
    if (this.$active) {
      this.$active.unsubscribe();
    }

    if (this.$route) {
      this.$route.unsubscribe();
    }

    if (this.$changes) {
      this.$changes.unsubscribe();
    }

    this.chat.activeChat  = undefined;
    this.chat.activePlace = undefined;
    this.chat.activeText  = undefined;
    this.chat.activeReply = undefined;
  }

  toggleMembers(set?: boolean) {
    if (set === undefined) {
      this.membersInternal = !this.members;
    }
    else {
      this.membersInternal = set;
    }
  }

  togglePlaces(set?: boolean) {
    if (set === undefined) {
      this.content = !this.content;
    }
    else {
      this.content = !set;
    }
  }
  profile() {
    this.router.navigate(['/cabinet/profile']);
  }

  client_last(c: Chat) {
    // return c.msg.reduce((a,b)=>{
    //   if (b.date && (b as ChatMessageUser).text !== undefined) {
    //     const pret = moment(b.date);
    //     const m = moment.max(a, pret);
    //     console.log(a.format('LL'));
    //     console.log(pret.format('LL'));
    //     console.log(m.format('LL'));
    //     return m;
    //   }
    //   else
    //     return a;
    // }, moment(c.msg[0].date)).format('L');

    const lastClientMsg = c.msg.reverse().find(msg=>(msg as ChatMessageUser).text!==undefined && msg.date);
    if (lastClientMsg)
      return moment(lastClientMsg.date).fromNow();
    else
      return '';
  }
}
