import {Component, Input, OnChanges, OnInit} from '@angular/core';
import * as moment from 'moment';
import {ChatInterface} from "../../utils/messages";
import ChartSerie = ChatInterface.ChartSerie;
import ChartChart = ChatInterface.ChartChart;
import ChartValue = ChatInterface.ChartValue;

@Component({
  selector: 'app-value-graph',
  templateUrl: './value-graph.component.html',
  styleUrls: ['./value-graph.component.scss']
})
export class ValueGraphComponent implements OnChanges {

  @Input() title = '';
  @Input() interval = "за месяц";
  @Input() series: ChartChart;
  @Input() suffix = '';

  get values(): ChartValue[] {
    return this.series.series[0].series;
  }

  get last(): ChartValue {
    return this.values[this.values.length-1];
  }

  get prev(): ChartValue {
    return this.values[this.values.length-2];
  }

  value: number;
  delta: number;
  interval_string;

  colorScheme = {
    domain: ['#33b4ff']
  };

  multi: any[] = [];

  ngOnChanges() {
    if (this.series && this.series.series.length>0 && this.series.series[0].series) {
      this.multi = [{
        name: this.title,
        series: this.values,
      }];

      this.series.name = this.title;
      this.value = this.last.value as number;
      if (this.values.length >= 2) {
        this.delta = this.value - (this.prev.value as number);
        const last = moment(this.last.name);
        const prev = moment(this.prev.name);

        const duration = last.from(prev);
        this.interval_string = duration.toString();
      }
      else {
        this.delta = undefined;
        this.interval_string = undefined;
      }
    }
  }


}
