import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import {ChatInterface} from "../../../utils/messages";
import * as moment from "moment";
import PlaceStatisticsPerInterval = ChatInterface.PlaceStatisticsPerInterval;
import ChartValue = ChatInterface.ChartValue;

@Component({
  selector: 'app-details-bar-value',
  templateUrl: './details-bar-value.component.html',
  styleUrls: ['./details-bar-value.component.scss']
})
export class DetailsBarValueComponent implements OnChanges {

  @Input() stat: PlaceStatisticsPerInterval;
  @Input() query: string;
  @Input() suffix: string='';

  series: ChartValue[];

  ngOnChanges(changes: SimpleChanges) {
    this.series = this.extractStat(this.stat, this.query)
  }

  extractStat(stat: PlaceStatisticsPerInterval, query): ChartValue[] {
    let time = moment();
    return stat.stat.map((s, index) => ({
        name: time.subtract(index, stat.interval as moment.unitOfTime.DurationConstructor).format('L'),
        value: s[query] ? s[query] : 0
      })
    );

  }
}
